import Picture from './Picture.js'
import Price from './Price.js'
import Favorite from './Favorite.js'
import Comment from './Comment.js'
import TestData from './test_data.js'
import _ from '../underscore.js'

class Room {
	room_id = null;
	city = null; //
	address = null; //
	title = null; //
	number_of_beds = null; //
	number_of_living_room = null; //
	number_of_bedroom = null; //
	maximum_number_of_people = null; //
	areaage = null; //
	descriptions = null; //
	special_information = null; //
	washing_machine = null; //
	iron = null; //
	hair_dryer = null; //
	air_conditioner = null; //
	wifi = null; //wifi
	refrigerator = null; //
	television = null; //
	kettle = null; //
	microwave = null; //
	kitchen_ware = null; //
	ATM = null; //ATM
	park = null; //
	post_office = null; //
	convenience_store = null; //
	supermarket = null; //
	pictures = [];
	prices = [];
	favorites = [];
	Comments = [];

	static binding_data(room) {
		room.pictures = _.filter(TestData.getData().data.pictures, function(picture) {
			return picture.room_id == room.id
		})
		// console.log(room)
		room.prices = _.filter(TestData.getData().data.prices, function(price) {
			return price.room_id == room.id
		})
		room.favorites = _.filter(TestData.getData().data.favorites, function(favortie) {
			return favortie.room_id == room.id
		})
		room.comments = _.filter(TestData.getData().data.comments, function(comment) {
			return comment.room_id == room.id
		})
		return room;
	}
}


export default Room
